	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<p>Software documentation is really important to create modular, scalable and readable code. This allows you and your team to reuse code and don't start from the ground in every project.\nA C code module can be divided in the definition (.h file) and implementation (.c file). The purpose of the definition or header file is to define the implemented functions,\nlibraries used, macros and public variables. In the header file we document the implemented functions without using technical details and describe the purpose of the function, parameters, etc.</p>\n<p>The header file should begin with information about the license:</p>\n<pre><code class=\"language-c\">    /****************************************************************************\n     * Copyright (C) 2018 by Maximiliano Valencia                               *\n     *                                                                          *\n     * This file is part of Box.                                                *\n     *                                                                          *\n     *   Box is free software: you can redistribute it and/or modify it         *\n     *   under the terms of the GNU Lesser General Public License as published  *\n     *   by the Free Software Foundation, either version 3 of the License, or   *\n     *   (at your option) any later version.                                    *\n     *                                                                          *\n     *   Box is distributed in the hope that it will be useful,                 *\n     *   but WITHOUT ANY WARRANTY; without even the implied warranty of         *\n     *   MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the          *\n     *   GNU Lesser General Public License for more details.                    *\n     *                                                                          *\n     *   You should have received a copy of the GNU Lesser General Public       *\n     *   License along with Box.  If not, see &#x3C;http://www.gnu.org/licenses/>.   *\n     ****************************************************************************/\n</code></pre>\n<p>Then we include information about the file:</p>\n<pre><code class=\"language-c\">    /** \n     *  @file threads.h \n     *  @brief this header file will contain all required \n     *  definitions and basic utilities functions.\n     *\n     *  @author Maximiliano Valencia\n     *\n     *  @date 4/10/2018\n     */\n</code></pre>\n<p> And information about ever function:</p>\n<pre><code class=\"language-c\">    /*!\n     *  @brief  Slave thread to perform the sum.\n     *  @author Maximiliano Valencia\n     *  @date   4/10/2018\n     *  @pre    pthread_create() has been called\n     *  @param  ignored Void pointer to argument passed when thread was created\n     *          with pthread_create()\n     *  Detailed description\n     */\n</code></pre>\n<p>The technical documentation will be achieved by documenting the file entities (functions, typedefs, global variables...etc).<br>\nIn the other hand, all functions must have technical explanations documented using inline comments within the function body.     </p>","frontmatter":{"title":"Using doxygen for C code","author":"Maximiliano Valencia","date":"10 April, 2018"}}},"pathContext":{"slug":"/posts/doxygen_for_c/"}}

